import React from 'react';
import {
  Link,
  Routes,
  Route,
} from "react-router-dom";
import routes from '../router';
import { Layout, Menu } from 'antd';
import './styles.css';

const { Header, Content } = Layout;
const renderRoute = (item, index) => {
  return <Route key={index} path={item.path} element={<item.component imgUrl="image/1.jpg" />} />
}

const LayoutComponent = props => {

  const pathName = window.location.pathname
  const selectedKey = React.useMemo(() => {
    return routes.findIndex(x => x.path === pathName).toString();
  }, [pathName])

  return (
    <Layout className='layout'>
      <Header>
        <Menu theme="dark" mode="horizontal" defaultSelectedKeys={[selectedKey]}>
          {routes.map((_, index) => {
            const key = index;
            return <Menu.Item key={key}>
              <Link to={_.path}>{_.name}</Link>
            </Menu.Item>;
          })}
        </Menu>
      </Header>
      <Content>
        <div className={'site-layout-content'}>
          <Routes>
            {routes.map(renderRoute)}
          </Routes>
        </div>
      </Content>
    </Layout>
  );
};

export default LayoutComponent;