import React from 'react';
import { fabric } from 'fabric';
import { createStorage } from '../../ultis';

const HTMLCanvasElement = 'canvas-text-box';

function TextBox({ imgUrl }) {
  const [Canvas, setCanvas] = React.useState();
  const CanvasStorage = createStorage('CanvasStorage');

  React.useEffect(() => {
    // Set CanvasInit
    fabric.Image.fromURL(imgUrl, (img) => {
      const canvi = new fabric.Canvas(HTMLCanvasElement, {
        width: img.width,
        height: img.height,
        isDrawingMode: false,
        renderOnAddRemove: true,
        backgroundImage: imgUrl,
      });
      canvi.on('mouse:dblclick', (e) => handleDblclick(e, canvi));
      const CanvasObjects = CanvasStorage.get('CanvasObjects');
      if (CanvasObjects) {
        console.log(CanvasObjects);
        CanvasObjects.forEach((element) => {
          var rect = new fabric.IText('Text Box', element);
          canvi.add(rect);
        });
      }
      setCanvas(canvi);
    });

    return () => {
      if (Canvas) Canvas.dispose();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [imgUrl]);

  const handleSave = () => {
    const listData = Canvas.getObjects().map((item) => {
      const { top, left, width, height, angle, text, zoomX, zoomY } = item;
      return { top, left, width: width * zoomX, height: height * zoomY, angle, text };
    });
    CanvasStorage.set('ListInput', listData);
    CanvasStorage.set('CanvasObjects', Canvas.getObjects());
  };

  return (
    <div>
      <button onClick={handleSave}>save</button>
      <canvas style={{ border: '1px solid black' }} id={HTMLCanvasElement} />
    </div>
  );
}

export default TextBox;

//event handler
const handleDblclick = (e, canvi) => {
  const { pointer, target } = e;
  if (target) return;
  addText(canvi, pointer);
};
//Text
const addText = (canvas, pointer) => {
  const { x, y } = pointer;
  const text = new fabric.IText('Text Box', {
    top: y,
    left: x,
    editable: true,
    backgroundColor: '#fafafa',
    borderColor: 'blue',
  });
  canvas.add(text);
};
