import React from 'react';
import PropTypes from 'prop-types';
import { createStorage } from '../../ultis';

const RenderInput = ({ imgUrl }) => {
  const CanvasStorege = createStorage('CanvasStorage');
  const [ListInput, setListInput] = React.useState(CanvasStorege.get('ListInput') ?? []);

  const renderListInput = () => {
    return ListInput.map((item, index) => {
      const { top, left, width, height, angle, text } = item;
      console.log('ðŸš€ ~ item', item);
      return (
        <div
          key={index}
          style={{ position: 'absolute', top: `${top}px`, left: `${left}px`, width: `${width}px`, height: `${height}px`, backgroundColor: 'blue' }}
        ></div>
      );
    });
  };

  return (
    <div>
      <div style={{ position: 'relative' }}>
        <img src={imgUrl} alt="" />
        {/* <div style={{ position: 'absolute', backgroundColor: 'blue', width: 50, height: 50, top: '0px' }}></div> */}
        {renderListInput()}
      </div>
    </div>
  );
};

RenderInput.propTypes = {};

export default RenderInput;
