import React from 'react';
import { fabric } from 'fabric';
import { createStorage } from '../../ultis';

const HTMLCanvasElement = 'canvas';

function DemoCanvas({ imgUrl }) {
  const [Canvas, setCanvas] = React.useState();
  const refMouse = React.useRef({});
  const count = CreatCount();
  const CanvasStorage = createStorage('CanvasStorage');

  React.useEffect(() => {
    // Set CanvasInit
    fabric.Image.fromURL(imgUrl, (img) => {
      const canvi = new fabric.Canvas(HTMLCanvasElement, {
        width: img.width,
        height: img.height,
        isDrawingMode: false,
        renderOnAddRemove: true,
        preserveObjectStacking: true,
        backgroundImage: imgUrl,
      });
      canvi.on('mouse:down', (e) => handleMouseDown(e, refMouse, canvi));
      canvi.on('mouse:up', (e) => handleMouseUp(e, refMouse, canvi));
      canvi.on('mouse:move', (e) => handleMouseMove(e, refMouse, canvi));
      setCanvas(canvi);
    });

    return () => {
      if (Canvas) Canvas.dispose();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [imgUrl]);
  //#region event handler
  const handleMouseMove = (e, refMouse, canvi) => {
    const buttons = e.e.buttons;
    const { target } = e;
    //when moving the rectangle
    if (target && buttons === 1) {
      const { name, top, left } = target;
      const [type, number] = name.split('-');
      //
      const textObj = canvi.getObjects().find((x) => x.name === `text-${number}`);
      //
      textObj.set({ ...textObj, top, left });
    }
  };
  const handleMouseDown = (e, refMouse, canvi) => {
    Object.assign(refMouse.current, { down: e });
  };
  //
  const handleMouseUp = (e, refMouse, canvi) => {
    const down = refMouse.current.down;
    const up = e;
    //
    if (!(down.target || up.target)) {
      addRectText(canvi, down.pointer, up.pointer);
    }
    Object.assign(refMouse.current, { up: e });
  };
  //
  const addRectText = (canvi, pointerDown, pointerUp) => {
    const width = Math.abs(pointerUp.x - pointerDown.x);
    const height = Math.abs(pointerUp.y - pointerDown.y);
    const [top, left] = [Math.min(pointerDown.y, pointerUp.y), Math.min(pointerDown.x, pointerUp.x)]; //coordinates closest to the origin

    if (!(width * height)) return null;
    if (width < 50 && height < 30) return null; //Smallest size

    const index = count.next();

    const rect = new fabric.Rect({
      name: `rect-${index}`,
      width,
      height,
      top,
      left,
      ...initialRect,
    });
    //
    const text = new fabric.Textbox('Text', {
      name: `text-${index}`,
      top,
      left,
      width,
      ...initialText,
    });
    canvi.add(rect, text);
  };
  //
  const handleSave = () => {
    const listObj = Canvas.getObjects();
    const listRect = listObj.filter((x) => x.name.includes('rect'));
    const listText = listObj.filter((x) => x.name.includes('text'));

    const ListInput = listRect.map(({ top, left, width, height, angle, zoomX, zoomY }, index) => ({
      top,
      left,
      width: width * zoomX,
      height: height * zoomY,
      angle,
    }));
    listText.forEach(({ text }, index) => {
      Object.assign(ListInput[index], { text });
    });
    CanvasStorage.set('ListInput', ListInput);
    // CanvasStorage.set('CanvasObjects', Canvas.getObjects());
  };
  //#endregion event handler
  return (
    <div>
      <button onClick={handleSave}>save</button>
      <canvas style={{ border: '1px solid black' }} id={HTMLCanvasElement} />
    </div>
  );
}

export default DemoCanvas;
//*************** */
function CreatCount() {
  let n = 0;
  return {
    next: () => n++,
    reset: () => (n = 0),
  };
}
//#region initial data
const initialRect = {
  fill: 'rgba(255,255,255,0.7)',
  stroke: 'blue',
  strokeWidth: 1,
  strokeUniform: true,
};
const initialText = {
  fontSize: 24,
  editable: true,
  borderColor: 'blue',
  // backgroundColor: 'rgba(255,255,255,0.9)',
  hasControls: false,
  lockMovementX: true,
  lockMovementY: true,
  lockScalingX: true,
  lockScalingY: true,
};
//#region

//
const addRect = (canvi, pointerDown, pointerUp) => {
  const width = Math.abs(pointerUp.x - pointerDown.x);
  const height = Math.abs(pointerUp.y - pointerDown.y);
  const [top, left] = [Math.min(pointerDown.y, pointerUp.y), Math.min(pointerDown.x, pointerUp.x)]; //coordinates closest to the origin

  if (!(width * height)) return null;

  const rect = new fabric.Rect({ width, height, top, left, fill: 'transparent', stroke: 'blue', strokeWidth: 1, strokeUniform: true });
  canvi.add(rect);
  // canvi.renderAll();
};
